;(function() {
    function Bill(params) {
        this.init(params);
    }

    Bill.prototype = {
        init: function (params) {
            this.products = params['products'] || [];
            this.paid_till = new Date(1e3 * params['paid_till']);

            if (params['bonus_months'] && params['bonus_period']) {
                this.bonus = {
                    months: params['bonus_months'] || 0,
                    period: params['bonus_period'] || 0
                };
            }

            this.lang = {
                bill_date: params['l_bill_date'],
                bill_months: params['l_bill_months'],
                bill_text: params['l_bill_text'],
                bill_comment: params['l_bill_comment'],
                bill_cost_format: params['l_bill_cost_format'],
                bill_cost_format_full: params['l_bill_cost_format_full'],
                bill_bonus: params['l_bill_bonus'],
                num: params['l_num']
            };

            this.currency = {
                sign: params['currency_sign'],
                id: params['currency_id']
            };

            this.elements = {
                product: document.getElementById(params['product_el'] || "product_field"),
                period: document.getElementById(params['period_el'] || "period_field"),
                license: document.getElementById(params['license_el'] || "license_col"),
                stripe: document.getElementById(params['stripe_el'] || "stripe-form")
            };
            if (this.elements['stripe']) this.stripe = {
                key: params['stripe_key'],
                name: params['stripe_name'],
                action: params['stripe_action']
            };

            if (!this.elements['period'] && params['period_months'] && params['period_title']) {
                this.period = {
                    months: params['period_months'],
                    title: params['period_title'],
                    value: params['period_value'] || 1
                };
            }

            document.getElementById("pp_btn").addEventListener("click", function(e) {
                if (!e.target.style.opacity) {
                    e.target.style.opacity = 0.5;
                    document.getElementById('charge').submit();
                }
                e.preventDefault();
            });

            this.elements.product.addEventListener("change", this.update.bind(this));
            if (this.elements['period']) this.elements.period.addEventListener("change", this.update.bind(this));
            this.update();

            document.getElementById('bill-table').className = '';
        },

        date_format: function(d) {
            var dd = d.getDate();
            dd < 10 && (dd = "0" + dd);
            var yy = d.getFullYear();

            return this.lang['bill_date'].replace("d", dd).replace("y", yy).replace("m", this.lang['bill_months'][d.getMonth()]);
        },

        number_wc: function(x) {
            return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")
        },

        pf: function(x) {
            return parseFloat(Math.round(100 * x) / 100).toFixed(2)
        },

        cost: function(format, value) {
            return format.replace(/:VALUE:/g, value).replace(":SIGN:", this.currency['sign']).replace(":CURRENCY:", this.currency['id']);
        },

        lng: function (x, values) {
            if (values.length == 2) {
                return x > 1 ? values[1] : (x ? values[0] : values[1]);
            }
            var cases = [2, 0, 1, 1, 1, 2];
            return values[(x % 100 > 4 && x % 100 < 20) ? 2 : cases[Math.min(x % 10, 5)]];
        },

        license_text: function(data) {
            return this.lang['bill_text']
                .replace(":SERVICES:", this.number_wc(data.services))
                .replace(':SERVICES_TITLE:', this.lng(data.services, this.lang['num']['services']))
                .replace(':SERVICES_TITLE_2:', this.lng(data.services, this.lang['num']['services2']))
                .replace(":CALLS:", this.number_wc(data.calls))
                .replace(":FROM:", this.date_format(data.from))
                .replace(":TO:", this.date_format(data.to))
                .replace(":COST:", data.cost)
                .replace(":SIGN:", data.sign);
        },

        license_comment: function(data) {
            return this.lang['bill_comment']
                .replace(":SERVICES:", this.number_wc(data.services))
                .replace(':SERVICES_TITLE:', this.lng(data.services, this.lang['num']['services']))
                .replace(':SERVICES_TITLE_2:', this.lng(data.services, this.lang['num']['services2']))
                .replace(":CALLS:", this.number_wc(data.calls))
                .replace(":COST:", data.cost)
                .replace(":PERIOD:", data.period);
        },

        update: function() {
            var product_id = this.elements['product'].value;
            var product_cost = 0;
            var currency_sign = '';
            var product_calls = 0;
            var product_services = 0;

            for (var i = 0; i < this.products.length; i++) {
                if (this.products[i].id == product_id) {
                    product_cost = this.products[i]['cost'];
                    product_calls = this.products[i]['calls'];
                    product_services = this.products[i]['services'];
                    currency_sign = this.products[i]['sign'];
                    break;
                }
            }

            if (this.elements['period']) {
                this.period = {
                    months: this.elements['period'].value,
                    title: this.elements['period'].options[this.elements['period'].selectedIndex].text,
                    value: this.elements['period'].value
                };
            }

            document.getElementById('pp_tariff_id').value = product_id;
            document.getElementById('pp_period').value = this.period['value'];

            var pm;
            var bonus_enable = false;
            if (this.bonus && this.period['months'] >= this.bonus.period) {
                pm = new Date(new Date(this.paid_till).setMonth(this.paid_till.getMonth() + parseInt(this.period['months']) + parseInt(this.bonus.months)));
                bonus_enable = true;
            } else {
                pm = new Date(new Date(this.paid_till).setMonth(this.paid_till.getMonth() + parseInt(this.period['months'])));
            }

            this.elements['license'].innerHTML = this.license_text({
                calls: product_calls,
                services: product_services,
                cost: product_cost,
                sign: currency_sign,
                from: this.paid_till,
                to: pm
            });
            if (bonus_enable) this.elements['license'].innerHTML += this.lang['bill_bonus'];

            document.getElementById('months_col').innerHTML = this.period['title'];
            if (document.getElementById('services_col')) document.getElementById('services_col').innerHTML = product_services.toString();

            var total_cost = this.period['value'] * product_cost;
            var comment = this.license_comment({
                services: product_services,
                calls: product_calls,
                cost: '$' + total_cost,
                period: this.period['title']
            });

            document.getElementById('cost_col').innerHTML = this.cost(this.lang['bill_cost_format'], this.pf(total_cost));
            document.getElementById("total_col").innerHTML = this.cost(this.lang['bill_cost_format'], total_cost);
            document.getElementById("total_f").innerHTML = this.cost(this.lang['bill_cost_format_full'], this.pf(total_cost));

            if (this.stripe) {
                var script = document.createElement('script');
                script.src = "https://checkout.stripe.com/checkout.js";
                script.className = "stripe-button";
                script.setAttribute("data-key", this.stripe['key']);
                script.setAttribute("data-amount", (100 * total_cost).toString());
                script.setAttribute("data-name", this.stripe['name']);
                script.setAttribute("data-description", comment);
                script.setAttribute("data-image", "/images/cleantalk-logo-128.png");
                this.elements['stripe'].innerHTML = '';
                this.elements['stripe'].appendChild(script);

                document.stripe_form.action = this.stripe['action']
                    .replace(":PERIOD:", this.period['value'])
                    .replace(":PRODUCT:", product_id);
            }
        }
    };

    window.Bill = Bill;
}());
