;(function () {
    window.addEventListener('load', function () {
        var cookie_name = 'hide_annotation';
        var annotation = document.getElementById('annotation');
        if (!annotation) return;

        var cp_mode = annotation.getAttribute('data-id');
        annotation.getElementsByClassName('close')[0].addEventListener('click', function () {
            var cookie = cp_mode;
            if (Cookies.contains('hide_annotation')) {
                cookie = Cookies.get('hide_annotation') + ',' + cp_mode;
            }
            Cookies.set(cookie_name, cookie, {
                expires: 86400 * 30,
                path: '/my'
            });

            annotation.style.display = 'none';
        });
    });
}());
