;(function () {
    function ApiKey(params) {
        this.init(params);
    }

    ApiKey.prototype = {
        init: function (params) {
            this.elements = {
                api_key: document.getElementById('api-key'),
                get_api_key: document.getElementById('get-api-key'),
                button: document.getElementById('api-key-button')
            };

            this.lang = {
                api_get_key: params['l_api_get_key']
            };

            this.email = params['email'];

            if (this.elements['api_key']) {
                var api_key = this.elements['api_key'].getAttribute('data-key');
                this.elements['button'].addEventListener('click', function () {
                    this.elements['api_key'].innerHTML = '<code style="font-size:1.2em;display:block;text-align:center">' + api_key + '</code>';
                }.bind(this));
            } else if (this.elements['get_api_key']) {
                this.elements['button'].addEventListener('click', function () {
                    if (~e.target.className.indexOf('disabled')) {
                        e.target.blur();
                        e.target.className += ' disabled';
                        e.target.innerHTML = this.lang['api_get_key'] +' <img src="https://cdn.cleantalk.org/images/loading.gif" width="16" height="16">';
                        this.request();
                    }
                }.bind(this));
            }
        },

        request: function () {
            var xhr = new XMLHttpRequest();
            var q = 'email=' + encodeURIComponent(this.email) + '&product_name=' + encodeURIComponent('database_api');

            xhr.open('GET', '/my/?get_api_key=1', true);

            xhr.onreadystatechange = function () {
                if (xhr.readyState != 4) return;

                if (xhr.status != 200) {
                    e.target.className = 'btn btn-danger disabled';
                    e.target.innerHTML = this.lang['api_get_key'] + ': ERROR';
                } else {
                    var response = JSON.parse(xhr.response);
                    if (!response || response['error']) {
                        e.target.className = 'btn btn-danger disabled';
                        e.target.innerHTML = this.lang['api_get_key'] + ': ERROR';
                    } else {
                        this.elements['get_api_key'].innerHTML = '<code style="font-size:1.2em;display:block;text-align:center">' + response['data']['auth_key'] + '</code>';
                    }
                }
            }.bind(this);

            xhr.send(q);
        }
    };

    window.ApiKey = ApiKey;
}());
